@import url('https://fonts.googleapis.com/css2?family=Vazirmatn:wght@500&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  font-family: 'Vazirmatn', sans-serif;
}

body {
  background-color: #171010;
  color: #F7F5EB;
  margin: 0rem 1rem;
}

#header {
  width: 100%;
  justify-content: center;
  display: flex;
}

.header-massage {
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 21px;
  display: flex;
  height: 3.5rem;
  justify-content: center;
  align-items: center;
  border-radius: 16px;
  background-color: #191919;
  color: #EAE0DA;
  padding: 0 1.5rem;
}

#server {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  height: 4rem;
  justify-content: space-between;
  align-items: center;
  background-color: #191919;
  border-radius: 16px;
  padding: 0 1.5rem 0 .75rem;
  margin-bottom: .75rem;
}

.copy-btn {
  width: 4rem;
  height: 2rem;
  background-color: #E94560;
  border-radius: 12px;
  color: #171010;
}

.copy-btn:active {
  background-color: #E94560;
  opacity: .2;
  animation: copy-btn .2s alternate;
}

@keyframes copy-btn {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: .2;
  }
}

#bar {
  width: calc(100% - 4rem);
  background-color: rgba(23, 16, 16, .6);
  backdrop-filter: blur(10px);
  height: 5rem;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  margin-bottom: 3.5rem;
  display: flex;
  justify-self: center;
  align-self: center;
  justify-content: space-between;
  align-items: center;
  padding: .75rem;
  border-radius: 32px;
}

.servers-homepage, .education-homepage {
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.servers-homepage {
  color: #EAE0DA;
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.education-homepage {
  color: #EAE0DA;
  width: 50%;
  height: 3.5rem;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}

.nav-btn-left {
  background-color: #E94560;
  width: calc(50% - .75rem);
  height: 3.5rem;
  border-radius: 16px;
  position: absolute;
  left: 0;
  z-index: -1;
  margin-left: .75rem;
  transform: translate(0);
  animation: nav-btn-to-left .5s;
}

.nav-btn-right {
  background-color: #E94560;
  width: calc(50% - .75rem);
  height: 3.5rem;
  border-radius: 16px;
  position: absolute;
  left: 100%;
  transform: translate(-107%);
  z-index: -1;
  margin-right: 1rem;
  animation: nav-btn-to-right .5s;
}

.nav-btn-black-right {
  color: #171010;
}

.nav-btn-black-left {
  color: #171010;
}

#education {
  display: none;
}

.change-color-div-hide {
  display: none;
}

.change-color-div {
  width: calc(100% - 2rem);
  background-color: rgba(23, 16, 16, .8);
  backdrop-filter: blur(12px);
  border-radius: 32px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%) translateY(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: .75rem;
  animation: change-color-div .2s alternate;
}  

@keyframes change-color-div {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100;
  }
}

.change-color-div-buttons {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  margin-top: .5rem;
}

.change-back-btn, .remove-back-btn {
  width: 100%;
  height: 3rem;
  border-radius: 12px;
  font-size: 14px;
  color: #F7F5EB;
}

.remove-back-btn {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #E94560;
}

.change-back-btn {
  flex: 2;
  background-color: #E94560;
}

#change-color-input, #change-servers-color-input {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 4px;
}

.change-color-div-options {
  display: flex;
  flex-direction: row-reverse;
  font-size: 16px;
  width: 20rem;
  justify-content: space-between;
  height: 5rem;
  align-items: center;
  padding: 0 2rem;
}

.change-color-div-options label {
  color: #E94560;
}

.change-color-div-options p {
  margin-left: 2rem;
}

#change-servers-color-input input[type="color"] {
  border: none;
}


#loader {
  width: 100%;
  height: 100vh;
  position: fixed;
  left: 0;
  z-index: +3;
  background-color: #F7F5EB;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

#footer {
  width: 100%;
  display: flex;
  justify-content: center;
  opacity: .5;
  font-size: 12px;
}

@keyframes nav-btn-black-right {
  0% {
    color: #EAE0DA;
  }
  100% {
    color: #171010;
  }
}

@keyframes nav-btn-to-right {
  0% {
    margin-left: .75rem;
    position: absolute;
    left: 0;
    transform: translate(0);
  }
  100% {
    margin-right: 1rem;
    position: absolute;
    left: 100%;
    transform: translate(-107%);
  }
}

@keyframes nav-btn-to-left {
  0% {
    background-color: #E94560;
    width: calc(50% - .75rem);
    height: 3.5rem;
    border-radius: 16px;
    position: absolute;
    left: 100%;
    transform: translate(-107%);
    z-index: -1;
    margin-right: 1rem;
  }
  100% {
    background-color: #E94560;
    width: calc(50% - .75rem);
    height: 3.5rem;
    border-radius: 16px;
    position: absolute;
    left: 0;
    z-index: -1;
    margin-left: .75rem;
    transform: translate(0);
  }
}